<template>
  <div class="flex flex-wrap w-full items-center">
  
    <form class="w-full" @submit.prevent="fetchResults">
    
        <input class="block m-auto border border-solid my-5 w-1/2 p-2 border-red-500" type="text" v-model="url" placeholder="Enter youtube link..">
        
        <button class="block m-auto my-5 border border-solid border-green-600 bg-green-300 p-4 rounded-lg" type="submit"> Get Download Link</button>
        
    
    </form>
    
    <Results v-if="link" :url="url"/>
    
    
  </div>
</template>

<script>
import Results from './Results.vue'

export default {
  name: 'Main',
  components:{Results},
  data(){
    return{
       url:"",
       link:null
    }
  },
  methods:{
    fetchResults(){
        if(this.link){
            this.link = null
        }
        else{
            this.link=this.url
        }
    }
  }
}
</script>

