<template>
  <div v-if="status" class="block md:flex flex-wrap my-4 p-4 border-2 border-solid border-green-300 bg-green-100 w-full max-h-screen overflow-y-auto">
    <div class="flex-1">
        <img :src="data.thumb" class="block m-auto w-auto h-auto content-center" alt="video thumbnail">
        <h1 class="p-4 m-auto my-2 text-center text-2xl"> {{data.title}}</h1>
        <h2 class="my-2 text-left text-xl"> Category: {{data.category}} </h2>
        <h2 class="my-2 text-left text-xl"> Published on: {{data.published}} </h2>
        <h2 class=" my-2 text-left text-xl"> Published by: {{data.userName}} </h2>
        <h2 class=" my-2 text-left text-xl"> Description:</h2>
        <p class="w-full h-64 overflow-y-auto text-justify p-4 border border-solid"> {{data.description}}</p>

    </div>
    <div class="flex-1">
        <div class="m-auto my-4 w-full h-auto" v-for="stream in data.streams">
            <Streams :stream="stream"/>
        </div>
    </div>
  </div>
   <div v-else class="m-auto my-4 p-4 border-2 border-solid border-green-300 bg-green-200 w-full max-h-screen text-center">
    <p> {{msg}} </p>
  </div>
</template>

<script>
const axios = require('axios')
import Streams from './Streams.vue'

export default {
  name: 'Results',
  props:['url'],
  components:{Streams},
  data(){
    return{
        data:null,
        status:null,
        msg:"Processing your request",
        apiBase:"https://my-youtube-download.herokuapp.com"
    }
  },
  async mounted(){
        try{
            var r = await axios(
                {
                    url:this.apiBase+'/api/getyt',
                    method: 'post',
                    data:{url:this.url}
                }
            )
            
            this.status = r.data.status
            this.msg = r.data.msg
            this.data = r.data.data
        }
        catch(err){
            this.msg="Hmm an error occoured. the link you provided might not be valid or its not downloadable"
        }
    }
}
</script>

